*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
  font-family: inherit;
}

:root {
  --boldgray: hsl(0, 5%, 25%);
  --lightgray: hsl(0, 0%, 92%);
  --gray: hsl(0, 0%, 55%);
  --gradient-lighter: #00dbde;
  --gradient-darker: #fc00ff;
}

body {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

ul {
  list-style: none;
}

body {
  font-family: "Barlow Semi Condensed", sans-serif;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 20px;
  color: #242b37;
}
